/*At a certain width make it so it is vertical and at bigger width make it horizontal */
.videoCallGrid {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.controlsGrid {
  height: 5%;
  width: 20%;
  display: flex;
  flex-direction: column;
  margin-left: 20%;
  margin-right: auto;
  margin-bottom: 1%;
  float: left;
}

.videoContainer {
  height: 95%;
  width: 60%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: rgb(235, 235, 235);
  margin-left: auto;
  margin-right: auto;
  border-style: solid;
  border-width: 5px;
  border-radius: 10px;
  border-color: rgb(219, 219, 219);
  padding: 10px;
}

.videoCallInsAndIng {
  margin-left: auto;
  margin-right: auto;
  margin-top: 2%;
  background-color: rgb(235, 235, 235);
  border-style: solid;
  border-width: 5px;
  border-radius: 10px;
  border-color: rgb(219, 219, 219);
}
.videoCallTitleContainer {
  width: 80%;
  font-family: helvetica, sans-serif;
  text-align: left;
  float: left;
  display: flex;
  font-weight: bold;
  margin-top: 1.5%;
  margin-left: 10%;
  margin-right: 10%;
}
.videoCallTitle {
  width: 50%;
  float: left;
  display: flex;
  font-size: 20pt;
  margin-left: 10%;
  margin-right: 10%;
}
.videoCallIngredientSection {
  position: relative;
  max-height: 100%;
  background-color: rgb(255, 255, 255);
  margin-bottom: auto;
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 1%;
  /*box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);*/
  background-color: inherit;
  float: left;
  text-align: left;
  display: flex;
  width: 80%;
}
.videoCallInstructionsContainer {
  position: relative;
  width: 50%;
  height: auto;
  background-color: rgb(255, 255, 255);
  text-align: left;
  float: right;
  display: flex;
  margin-left: 10%;
  margin-right: 10%;
  background-color: inherit;
}
.videoCallIngredient {
  margin-top: 2%;
  width: 80%;
  background-color: inherit;
}
.videoCallInstructionsContainer {
  position: relative;
  width: 50%;
  height: auto;
  background-color: inherit;
  text-align: left;
  float: right;
  display: flex;
}
