.startVideoCallContainer {
  position: relative;
  height: auto !important;
  height: 100%;
  min-height: 76.9vh;
  display: flex;
  margin-bottom: 100px;
  padding-top: 100px;
  width: 100%;
  flex-direction: column;
}

.startVideoCallContent {
  width: 80%;
  margin-left: auto;
  display: flex;
  margin-right: 10%;
  margin-top: 8%;
}

.slideshow {
  width: 70%;
  max-height: 480px;
  min-height: 480px;
  margin: 0 auto;
  overflow: hidden;
  max-width: 720px;
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
}

.slideshowSlider {
  white-space: nowrap;
  transition: ease 1000ms;
  height: 100%;
  position: relative;
  width: 100%;
}

.slide {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background-position: center;
  background-size: cover;
  display: inline-block;
  color: rgb(255, 255, 255);
  font-size: 24pt;
  font-style: oblique;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  text-align: left;
  text-shadow: 2px 2px 2px #000000;
}

.startCallButtonContainer {
  width: 30%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.startCallButtonItem {
  width: 100%;
  height: 50%;
}
.startCallButton {
  margin-top: 10%;
  margin-bottom: 5%;
  width: 80%;
  height: 40%;
  font-size: 20pt;
  padding: 8%;
  float: left;
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
}
.startCallButton:hover {
  cursor: pointer;
  color: rgb(235, 144, 41);
  border-color: rgb(235, 144, 41);
  border-width: 2px;
}
.createCallErrorText {
  font-style: oblique 10deg;
  color: #c96565;
  margin-top: 1.5%;
  margin-right: auto;
}

.chooseRecipe {
  margin-top: 4%;
}

.chooseRecipeText {
  margin-bottom: 0.5%;
}
