.customul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #fff;
  /*background-color: #bbd6b8;*/
  position: fixed;
  width: 100%;
  z-index: 2;
  margin-bottom: 2%;
  box-shadow: 0 2px 1px 0 rgba(0, 0, 0, 0.2);
}

li {
  float: left;
  display: inline;
  font-size: 14pt;
  line-height: 20px;
}

li a {
  display: block;
  color: #353535;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  line-height: 20px;
}

li a:hover {
  /*background-color: #94af9f;*/
  color: rgb(235, 144, 41);
  line-height: 20px;
}

.leftli {
  float: right;
  line-height: 20px;
}

.rightli {
  float: right;
  line-height: 20px;
  margin-right: 4%;
}

.banquetli {
  font-size: 20pt;
  margin-left: 4%;
  margin-right: 2%;
  line-height: 20px;
  font-weight: bold;
}
.banquetli:hover {
  color: rgb(235, 144, 41);
}
