.recipeCard {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.4s;
  flex: 0 1 24%;
  margin-top: 6%;
  width: 100%;
  padding: 2%;
  padding-bottom: 8%;
  height: 100% !important;
  min-height: 100%;
  transition: all 0.6s;
}
.recipeCard:hover {
  box-shadow: -8px -8px 1px 2px rgb(235, 144, 41);
  cursor: pointer;
}
.recipeCard:hover .titleRecipeCard {
  color: rgb(235, 144, 41);
}

.titleRowRecipeCard {
  display: inline-block;
  width: 100%;
}

.customImgRecipeCard {
  width: 400px;
  height: 400px;
  object-fit: cover;
  position: relative;
  border-radius: 5px;
  transition: all 0.4s;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
  overflow: hidden;
}
.customImgRecipeCard:hover {
  /*transform: translateX(10px);
  box-shadow: 10px 10px 0 0 rgb(139, 199, 240) inset;*/
  transform: scale(1.05);
}

@media screen and (min-width: 1632px) {
  .customImgRecipeCard {
    width: 400px;
    height: 400px;
    object-fit: cover;
    position: relative;
    border-radius: 5px;
    transition: all 0.4s;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    overflow: hidden;
  }

  .customImgRecipeCardAccount {
    width: 300px;
    height: 300px;
    object-fit: cover;
    position: relative;
    border-radius: 5px;
    transition: all 0.4s;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    overflow: hidden;
  }
  .customImgRecipeCard:hover,
  .customImgRecipeCardAccount:hover {
    transform: scale(1.05);
  }
  .titleRecipeCard {
    font-weight: bold;
    display: inline-block;
    font-size: 28px;
    padding-right: 8px;
    cursor: pointer;
    transition: all 0.4s;
    position: relative;
    width: 90%;
    text-align: left;
  }
  .titleRecipeCard:hover {
    font-weight: bold;
    display: inline-block;
    padding-left: 8px;
    cursor: pointer;
    /*transform: scale(1.08);*/
    color: rgb(235, 144, 41);
  }
}

