.homePageContainer {
  width: 100%;
  position: relative;
  height: auto !important;
  height: 100%;
  min-height: 76.9vh;
  margin-bottom: 100px;
  padding-top: 60px;
  display: grid;
  grid:
    "image image text"
    "text2 image2 image2";
}

.leftImageContainer {
  grid-area: image;
}

.textArea1 {
  grid-area: text;
  margin-left: -10%;
  text-align: center;
  margin-right: auto;
}

.textArea2 {
  grid-area: text2;
  margin-right: -10%;
  text-align: center;
  margin-left: auto;
}
.rightImageContainer {
  grid-area: image2;
}

.imageHome1 {
  width: 900px;
  height: 600px;
  float: left;
  object-fit: cover;
  overflow: hidden;
  margin-left: 2.5%;
  margin-top: 1%;
}
.imageHome2 {
  width: 900px;
  height: 600px;
  float: right;
  object-fit: cover;
  overflow: hidden;
  margin-right: 2.5%;
  margin-top: 1%;
}
